/* Apply a modern font */
body {
    font-family: 'Arial', sans-serif;
  }
  
  /* General container styling */
  .container {
    padding: 20px;
    max-width: 1200px; /* or your preferred max width */
    margin: auto;
  }
  
  /* Booking header styling */
  .booking-header {
    text-align: center;
    margin-bottom: 30px;
  }
  
  /* Doctor information styling */
  .doctor-info {
    background-color: #fffefe; /* A light grey background */
    padding: 20px;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  
  /* Time slots list */
  .time-slots {
    list-style: none;
    padding: 0;
  }
  
  .time-slots li {
    padding: 10px;
    border: 1px solid #ddd; /* A subtle border */
    margin-bottom: 10px;
    border-radius: 5px; /* Rounded corners */
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .time-slots li:hover,
  .time-slots li.selected {
    background-color: #e9ecef;
  }
  .time-slot-item.past {
    color: #ccc; 
    cursor: not-allowed;
    pointer-events: none; 
  }

  .past-time-slot {
    color: #ccc; /* Grey out past time slots */
    pointer-events: none; /* Disable interactions for past time slots */
    background-color: #f5f5f5; /* Light grey background */
  }
  .selected-time-slot {
    background-color: #4CAF50; /* A distinct background color */
    color: white; /* Change text color for better contrast */
    /* Add any additional styles you want for selected time slots */
  }
  
  /* Styles for selectable time slots */
  .selectable-time-slot {
    cursor: pointer;
    /* Add any additional styles you want for selectable time slots */
  }
  /* Booking actions styling */
  .booking-actions {
    display: flex;
    flex-direction: column;
    gap: 10px; /* Consistent spacing between elements */
  }
  
  /* Responsive button styling */
  .btn {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #0056b3; /* Example primary color */
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .btn:hover {
    background-color: #004494; /* A darker shade for hover state */
  }
  
  /* Responsive input and select styling */
  .ant-picker, .ant-select-selector {
    width: 100%; /* Full width */
    border-radius: 5px; /* Rounded corners */
  }
  
  /* Responsive layout adjustments */
  @media (max-width: 768px) {
    .container {
      padding: 10px;
    }
  
    .booking-actions {
      width: 100%;
    }
  }
  