.main {
  padding: 10px;
  height: 100vh;
  font-family: 'Arial', sans-serif; /* Modern font */
}

.layout {
  display: flex;
}
.sidebar {
  min-height: 100%;
  width: 300px;
 
  border-radius: 2px; /* Rounded corners */
  background-color: rgb(21, 171, 222); /* Lavender theme */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Modern shadow */
  margin-right: -1px;
 
  color: white;
  display: flex;
  flex-direction: column; /* Aligns items vertically */
}
/* Ensure the toggle button is always visible on mobile */
.sidebar-toggle {
  display: block; /* Show the toggle button */
  position: fixed;
  z-index: 30; /* Higher z-index to ensure visibility */
  top: 10px;
  left: 10px;
  border: none;
  /* Rounded corners */
  background-color: rgb(21, 171, 222); /* Lavender theme */
 /* Modern shadow */
  margin-right: 10px;
  color: white;
  padding: 10px;
  font-size: 1.5rem;
  color: white;
}

/* Styles for the open button when sidebar is visible */
.sidebar-toggle.open {
  left: calc(21% - 50px); 
  top: calc(10% - 55px);
   /* Move the button to the right when sidebar is open */
}

/* Hide sidebar initially on mobile */
.sidebar.inactive {
  transform: translateX(-110%);

 
}

/* Show sidebar on toggle */
.sidebar.active {
  transform: translateX(0);
  background-color: rgb(21, 171, 222); 
 
}

/* Media queries for mobile view */
@media (max-width: 768px) {
  .sidebar {
    position: fixed;
    z-index: 20;
    width: 80%; /* Adjust width as needed */
    height: 100%; 
    border-radius: 20px; /* Rounded corners */
  background-color: rgb(21, 171, 222); /* Lavender theme */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Modern shadow */
  margin-right: 20px;
  color: white;/* Full height sidebar */
    top: 0;
    left: 0;
    transition: transform 0.3s ease-in-out;
  }

  /* Adjust content padding to not overlap with the sidebar */
  .content {
    padding-top: 60px;
    background-color: #f5f5f5; /* Additional padding to avoid overlap */
  }
}


.content {
  width: 100%;
  height: 100%;
  background-color: #f5f5f5; /* Light background for the content area */
}

.header, .body {
  border-radius: 10px; /* Rounded corners */
  margin-bottom: 15px;

  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Modern shadow */
  background-color: white;
}

.header {
  height: 10vh;
  align-content: center;
}

.body {
  height: 85vh;
}

.logo h6 {
  font-size: 1.5rem;
  text-align: center;
  margin: 20px 5px;
  font-weight: bold; /* Bolder font for logo */
}

.menu {
  margin-top: 10px;
  align-items: center;
  flex-grow: 1; /* Allows the menu to take up available space */
}

.menu-item {
  margin-top: 30px;
  margin-left: 15px;
  align-items: center;
  transition: background-color 0.3s; /* Smooth transition for hover effect */
}

.menu-item a {
  color: white;
  text-decoration: none;
  margin-left: 15px;
  font-size: 1.2rem;
  display: flex; /* Aligns icon and text */
  align-items: center; /* Centers items vertically */
}

.menu-item i {
  font-size: 1.5rem; /* Slightly larger icons */
  margin-right: 15px;
  margin-left: 15px;
  align-items: center;
}

.menu-item:hover {
  background-color: rgba(255, 255, 255, 0.2); /* Hover effect for menu items */
}

.active {
  background-color: white;
  color: rgb(0, 0, 0); /* Lavender text for active items */
  padding: 10px;
}

.active a, .active i {
  color: rgb(20, 20, 20);
}

.header-content {
  display: flex;
  align-items: center;
  height: 50px;
  justify-content: flex-end;
  margin: 0 15px;
}

.header-content i {
  margin-right: 20px;
  font-size: 1.2rem;
}

.header-content a {
  text-decoration: none;
  font-size: 1.2rem;
  color: blue;
  margin: 0 10px;
  text-transform: uppercase;
}
