.appointment-search-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    margin: 20px 0;
  }
  
  .input-group {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  .date-input .date-picker,
  .doctor-id-input .doctor-id {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100%;
    margin-bottom: 5px;
  }
  
  .date-label,
  .doctor-id {
    text-align: center;
  }
  
  .search-button-container .search-button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #4CAF50; /* Green */
    color: white;
    cursor: pointer;
    width: 100%;
    box-sizing: border-box;
    transition: background-color 0.3s;
  }
  
  .search-button:hover {
    background-color: #45a049;
  }
  
  /* Responsive design */
  @media (max-width: 768px) {
    .appointment-search-container {
      width: 90%;
      margin: 20px auto;
    }
  }
  