.home-container {
 
    flex-wrap: wrap; 
    padding: 1rem;
    margin: auto;
    max-width: 1200px; /* Set a max-width for large screens */
  }
  
  .filters-container {
    display: flex;
    flex-wrap: wrap; /* Wrap on small screens */
    gap: 1rem; /* Spacing between filter elements */
    justify-content: center;
    margin-bottom: 2rem;
  }
  
  .state-select, .district-select {
    flex: 1; /* Flexible width */
  min-width: 150px; /* Flexible width */
    min-width: 150px;
    justify-content: center; /* Minimum width for small screens */
  }
  
  .lists-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr)); /* Responsive grid */
    gap: 1rem;
  }
  
  .clinic-row, .doctor-row {
    width: 100%;
  }
  
  /* Responsive design for smaller screens */
  @media (max-width: 768px) {
    .home-container {
      padding: 0.5rem;
    }
  
    .filters-container {
      flex-direction: column; /* Stack filters vertically on small screens */
      align-items: stretch; /* Align items for full width */
    }
  }

  @media (max-width: 1024px) {
    .card {
      flex-basis: calc(50% - 2rem); /* 2 cards in a row */
    }
  }
  
  @media (max-width: 768px) {
    .card {
      flex-basis: 100%; /* Full width for mobile */
      margin: 1rem 0; /* Adjust margin for mobile stacking */
    }
  }

  .card {
    cursor: pointer;
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
    /* Rest of your card styles... */
  }
  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    transform: translateY(-2px); /* Slight lift effect on hover */
  }
  
  .selected {
    border: 2px solid #4A90E2; /* Highlighted border for selected card */
  }
  
  